<template>
  <obd-card
    card-title="Zatia nemte iadneho dodvatea jedl"
    card-subtitle="Mete si vybra jednho z vho mesta"
  >
    <obd-table
      v-if="restaurants.length > 1"
      :data="restaurantsStr"
      :columns="JSON.stringify(columns)"
      :actions="JSON.stringify(tableActions)"
      @action="action"
    />
    <h3 v-else>utujeme, neevidujeme iadneho dodvatea z Vho mesta.</h3>
  </obd-card>
</template>

<script>
import axios from 'axios'

export default {
  data() {
    return {
      restaurants: '',
      tableActions: [
        {
          text: 'Vybra',
          action: 'selectContractor',
          color: '#f07b3f',
        },
      ],
      columns: [
        {
          key: 'restaurant',
          text: 'Retaurcia',
        },
        {
          key: 'city',
          text: 'Mesto',
        },
      ],
    }
  },
  computed: {
    restaurantsStr() {
      return JSON.stringify(this.restaurants)
    },
  },
  created() {
    this.loadRestaurants()
  },
  methods: {
    action(e) {
      switch (e.detail.action) {
        case 'selectContractor':
          this.createAgreement(e.detail.id)
          break
      }
    },

    createAgreement(restaurantId) {
      let response = confirm('Naozaj chcete vybra danho dodvatea?')

      if (response) {
        axios.defaults.headers.common['Authorization'] =
          this.$store.state.auth.tokenType + ' ' + this.$store.state.auth.token

        axios
          .post('/agreements', {
            restaurant_id: restaurantId,
          })
          .then(response => {
            this.restaurants = response.data.data
            this.flashSuccess(
              'Dodvate bol zvolen. Teraz muste poka na schvlenie.',
              {
                timeout: 3000,
              },
            )
            this.$emit('created-agreement')
          })
          .catch(error => {
            this.flashError(error.response.data.error)
          })
      }
    },

    loadRestaurants() {
      axios.defaults.headers.common['Authorization'] =
        this.$store.state.auth.tokenType + ' ' + this.$store.state.auth.token

      axios
        .get('/restaurants')
        .then(response => {
          this.restaurants = response.data.data
        })
        .catch(error => {
          this.flashError(
            'Nieo sa pokazilo, nebolo mon nata obsah strnky.<br>Skste obnovi strnku.',
          )
        })
    },
  },
}
</script>