<template>
  <div>
    <h2 class="title">akajce na schvlenie</h2>
    <obd-table
      v-if="clients.length"
      class="table"
      layout="fixed"
      :data="JSON.stringify(clients)"
      :columns="JSON.stringify(columns)"
      :actions="JSON.stringify(tableActions)"
      @action="action"
    />
    <h3 v-else>Nemte iadne nov iadosti</h3>
  </div>
</template>

<script>
export default {
  props: ['clients'],

  data() {
    return {
      columns: [
        {
          key: 'company',
          text: 'Klient',
        },
        {
          key: 'city',
          text: 'Mesto',
        },
      ],
      tableActions: [
        {
          text: 'Schvli',
          action: 'confirm',
          color: '#f07b3f',
        },
      ],
    }
  },

  methods: {
    action(e) {
      this.$emit(e.detail.action, e.detail.id)
    },
  },
}
</script>

<style lang="scss" scoped>
.title {
  margin-bottom: 0.3em;
}
.table {
  margin-bottom: 1.5em;
}
</style>