<template>
  <section class="section section--admin">
    <menu-add-new
      :active="newMealPopup"
      @closed="closePopup"
      @added-meal="addedMeal"
    />

    <menu-edit
      :active="editMealPopup"
      :mealId="editableId"
      @closed="closeEditPopup"
      @edited-meal="editedMeal"
    />

    <obd-card
      card-title="Menu"
      v-if="initialized"
    >
      <div slot="controls">
        <obd-button @click="togglePopup">Prida jedlo</obd-button>
      </div>

      <obd-table-menu
        :meals="JSON.stringify(meals)"
        @edit="openEditPopup"
        @delete="deleteMeal"
        v-if="meals.length"
      />
      <h3 v-else>Nemte iadne jedlo v ponuke</h3>
    </obd-card>
  </section>
</template>

<script>
import axios from 'axios'
// import MenuTable from './components/MenuTable'
import MenuAddNew from './components/MenuAddNew'
import MenuEdit from './components/MenuEdit'

export default {
  name: 'menuPage',
  components: {
    // MenuTable,
    MenuAddNew,
    MenuEdit,
  },

  data() {
    return {
      initialized: false,
      meals: [],

      newMealPopup: false,

      editMealPopup: false,
      editableId: '',
    }
  },

  mounted() {
    this.loadMeals()
  },
  methods: {
    loadMeals() {
      this.$store
        .dispatch('fetchMeals')
        .then(() => {
          this.meals = this.$store.getters.getMeals
          this.initialized = true
        })
        .catch(e => {
          this.flashError(
            'Nieo sa pokazilo, nebolo mon nata obsah strnky.<br>Skste obnovi strnku.',
          )
        })
    },

    togglePopup() {
      this.newMealPopup = !this.newMealPopup
    },

    closePopup() {
      this.newMealPopup = false
    },

    toggleEditPopup() {
      this.editMealPopup = !this.editMealPopup
    },

    addedMeal(meal) {
      this.meals.push(meal)
      this.closePopup()
    },

    editedMeal() {
      this.loadMeals()
      this.editMealPopup = false
    },

    openEditPopup(e) {
      this.editableId = e.detail
      this.editMealPopup = true
    },

    closeEditPopup() {
      this.editMealPopup = false
    },

    deleteMeal(e) {
      const mealId = e.detail
      let response = confirm('Naozaj chcete vymaza dan jedlo?')
      if (response) {
        axios.defaults.headers.common['Authorization'] =
          this.$store.state.auth.tokenType + ' ' + this.$store.state.auth.token

        axios
          .delete('/meals/' + mealId)
          .then(() => {
            let meals = this.meals.filter(meal => meal.id != mealId)
            this.meals = meals

            this.flashSuccess('Jedlo bolo spene zmazan.', {
              timeout: 3000,
            })
          })
          .catch(error => {
            console.log(error)
            this.flashError('Nieo sa pokazilo, skste to znova.', {
              timeout: 3000,
            })
          })
      }
    },
  },
}
</script>